import React from "react";
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import { SERVER_API, CHECKOUT_PAGE } from "config";
import { useSelector, useDispatch } from "react-redux";
import { toast } from "react-toastify";
import PageHeader from "components/PageHeader";
import Title from 'components/Title';
import CartManager from 'components/CartManager';
import { clearCart } from "store/cart";

import './style.scss';
const client = axios.create({ baseURL: SERVER_API });

export default function CartPage() {
    const cart = useSelector(state => state.cart);
    const dispatch = useDispatch();
    const navigate = useNavigate();


    const completeOrder = (e) => {
        e.preventDefault();
        let er = false;

        if (e.target.fullname.value.length < 1) {
            er = true;
            toast.error('Tên khách hàng không thể để trống');
        }
        if (e.target.phone.value.length < 10) {
            er = true;
            toast.error('Số điện thoại không đúng!');
        }
        if (e.target.address.value.length < 1) {
            er = true;
            toast.error('Địa chỉ không thể để trống');
        }

        if (!er) {
            const data = {
                fullname: e.target.fullname.value,
                phone: e.target.phone.value,
                address: e.target.address.value,
                orders: []
            }
            cart.items.forEach(el => {
                data.orders.push({
                    id: el.id,
                    name: el.name,
                    amount: el.amount
                })
            });
            client.post('/orders', data).then((res) => {
                toast.success('Đặt hàng thành công!');
                dispatch(clearCart());
                navigate(CHECKOUT_PAGE + '?phone=' + e.target.phone.value);
            }).catch(e => {
                if (e.response && e.response.data) {
                    toast.error(e.response.data.errors[0]);
                } else {
                    toast.error('Máy chủ gặp sự cố, vui lòng thử lại');
                }
            });
        }



    }

    return (
        <main style={{ minHeight: 'calc(100vh - 43px - 264px)' }}>
            <section className="clear--padding">
                <div className="container" style={{ backgroundColor: '#fff' }}>
                    <div className="gap"></div>
                    <Title title='GIỎ HÀNG' />
                    <div className="gap"></div>
                    <div className="flex">
                        <div className="col-sm-12 col-lg-12">
                            <CartManager />
                        </div>
                    </div>
                    <div className="gap"></div>
                    <div style={{ borderTop: '1px solid #ddd' }}></div>
                    <div className="gap"></div>
                    <form onSubmit={completeOrder}>
                        <div className="flex flex--gap">
                            <div className="col-sm-12 col-lg-8">
                                <div className="checkout-form">
                                    <div>
                                        <label className="uppercase">Tên khách hàng</label>
                                        <div className="gap--10"></div>
                                        <input type="text" className="input" name="fullname" placeholder="Họ và tên khách hàng" />
                                    </div>
                                    <div className="gap--20"></div>
                                    <div>
                                        <label className="uppercase">Số điện thoại</label>
                                        <div className="gap--10"></div>
                                        <input type="number" className="input" name="phone" placeholder="Nhập số điện thoại nhận hàng" />
                                    </div>
                                    <div className="gap--20"></div>
                                    <div>
                                        <label className="uppercase">Địa chỉ nhận hàng</label>
                                        <div className="gap--10"></div>
                                        <textarea type="text" className="input" name="address" placeholder="Nhập địa chỉ"></textarea>
                                    </div>
                                </div>
                            </div>
                            <div className="col-sm-12 col-lg-4">
                                <div className="pd pi--30 cart-total">
                                    <h5>HÓA ĐƠN</h5>
                                    <div className="gap--10"></div>
                                    <ul className="alt">
                                        <li className="flex x--between y--center">
                                            <div><h6>Sản phẩm</h6></div>
                                            <div><span>{new Intl.NumberFormat("de-DE").format(cart.totalPay) + ' VNĐ'}</span></div>
                                        </li>
                                        <li className="divider"></li>
                                        <li className="flex x--between y--center">
                                            <div><h6>Tổng cộng</h6></div>
                                            <div><h6>{new Intl.NumberFormat("de-DE").format(cart.totalPay) + ' VNĐ'}</h6></div>
                                        </li>
                                    </ul>
                                    <div className="gap"></div>
                                    <div className="divider"></div>
                                    <div className="gap--10"></div>
                                    <button className="btn black block" type="submit">
                                        Hoàn tất đơn hàng
                                    </button>
                                    <div className="gap"></div>
                                </div>
                            </div>
                        </div>
                    </form>
                    <div className="gap--50"></div>
                </div>
            </section>

        </main>
    )
}